import React from 'react';
import {NumberFormatPrice} from "../../NumberFormatFN";


export const GetPriceProductBasket = ({ ProductData, countProduct }) => {

    //console.log('   -------=============',ProductData);
    if(ProductData.price_discount !== false){
        return NumberFormatPrice(ProductData.price_discount.new_price*countProduct,0,'.',' ');
    }else{
        return NumberFormatPrice(ProductData.price*countProduct,0,'.',' ');
    }
};


export const GetPriceProductBasketDiscount = ( ProductData, countProduct ) => {

    //console.log('   -------=============',ProductData);
    if(ProductData.price_discount !== false){

        let Sum =0;
        Sum = (ProductData.price_discount.old_price*countProduct)-(ProductData.price_discount.new_price*countProduct);
        if(Sum<=0){Sum = 0;}
        return Sum;
    }else{
        return 0;
    }
};


export const GetPriceProductBasketVisual = ({ ProductData, countProduct, stylePrice,styleNewPrice,styleOldPrice , fontSize = 16, fontFamily = 'bold', priceBlock = "row" }) => {

    //console.log('   -------=============',ProductData);
    if(ProductData.price_discount !== false){
        return (
            <div style={{...styles.PriceBlock,...{flexDirection:priceBlock}}}>
                <div style={{...styles.Newprice,...styleNewPrice,...{fontWeight:fontFamily}}}>
                    {ProductData.price_discount.new_price*countProduct} 
                </div>
                <div style={{...styles.Oldprice,...styleOldPrice}}>
                    {NumberFormatPrice(ProductData.price_discount.old_price*countProduct,0,'.',' ')} 
                </div>
            </div>
        )
    }else{
        return (
            <div style={{...styles.PriceBlock,...{flexDirection:priceBlock}}}>
                <div style={{...{...styles.price,...stylePrice},...{fontSize:fontSize},...{fontWeight:fontFamily}}}>
                    {NumberFormatPrice(ProductData.price*countProduct,0,'.',' ')} 
                </div>
            </div>
        )
    }
};

export const GetPriceProductBasketPromokodeVisual = ({ BasketPromoDiscount, ProductId, typeProduct, stylePrice,styleNewPrice,styleOldPrice , fontSize = 16, fontFamily = 'bold', priceBlock = "row" }) => {

    let SuccesProduct = false;
    let NumberArrayKeyProduct = 0;

    BasketPromoDiscount.forEach((item,number)=>{
        //console.log(item,number);
        if(item.id_product === ProductId && item.type_product === typeProduct){
            if(item.sumPriceSale !== item.sumPriceDef){
                SuccesProduct = true;
                NumberArrayKeyProduct = number;
            }else{
                SuccesProduct = false;
                NumberArrayKeyProduct = number;
            }

            //console.log(NumberArrayKeyProduct);
        }
    });

    // console.log(BasketPromoDiscount,NumberArrayKeyProduct);

    if(SuccesProduct !== false){
        return (
            <div style={{...styles.PriceBlock,...{flexDirection:priceBlock}}}>
                <div style={{...styles.Newprice,...styleNewPrice,...{fontWeight:fontFamily}}}>
                    {NumberFormatPrice(BasketPromoDiscount[NumberArrayKeyProduct].sumPriceSale,0,'.',' ')} 
                </div>
                <div style={{...styles.Oldprice,...styleOldPrice}}>
                    {NumberFormatPrice(BasketPromoDiscount[NumberArrayKeyProduct].sumPriceDef,0,'.',' ')} 
                </div>
            </div>
        )
    }else{
        return (
            <div style={{...styles.PriceBlock,...{flexDirection:priceBlock}}}>
                <div style={{...{...styles.price,...stylePrice},...{fontSize:fontSize},...{fontWeight:fontFamily}}}>
                    {NumberFormatPrice(BasketPromoDiscount[NumberArrayKeyProduct].sumPriceDef,0,'.',' ')} 
                </div>
            </div>
        )
    }
};


export const GetPriceProductBasketNumber = (ProductData, countProduct, returnBoleanDiscount = false) => {

    let SumReturn = 0;
    let Bolean = false;

    if(ProductData.price_discount !== false){
        SumReturn = (ProductData.price_discount.new_price*countProduct);
        Bolean = true;
    }else{
        SumReturn = (ProductData.price*countProduct);
        Bolean = false;
    }

    if(returnBoleanDiscount ===true){
        return Bolean;
    }else{
        return SumReturn;
    }

};

export const GetTypePriceProductBasket = (ProductData = {}) => {

    if(ProductData.price_discount !== false){
        return true;
    }else{
        return false;
    }
};



