import React from 'react';

export const ConditionsPromokod = ({typeReturn = 1, promokodeErrorInfo})=>{

    // const promokodeErrorInfo = useSelector(state => state.post.promokodeErrorInfo);

    const getCHeckSum = ()=>{
        if(promokodeErrorInfo.showIconError && promokodeErrorInfo.errorSum.countSum>0){
            return (<div style={styles.TextInfoMini}><p>       :</p> <p style={styles.BoldText}>{promokodeErrorInfo.errorSum.countSum} </p></div>);
        }else{
            return false;
        }
    };


    const getCheckDiscountOtherProduct = ()=>{
        console.log(promokodeErrorInfo);
        if(promokodeErrorInfo.showIconError && promokodeErrorInfo.errorProduct.status === true){
            return (<div style={styles.TextInfoMini}><p>{promokodeErrorInfo.errorProduct.message}</p></div>);
        }else{
            return false;

        }
    };


    let ReturnData = false;

    switch (typeReturn) {
        case 1:
            ReturnData = getCHeckSum();
            break;
        case 2:
            ReturnData = getCheckDiscountOtherProduct();
            break;
        default: ReturnData = false;
    }


    return (ReturnData);
};


const styles = {
    BoldText:{
        fontWeight:"bold"
    },
    TextInfoMini:{
        backgroundColor:"#ff7300",
        fontSize: 14,
        color:"#FFFFFF",
        textAlign: "center",
        padding:5,
        margin: "10px 0"
    },
    TextInfoMiniOk:{
        backgroundColor:"#FFFFFF",
        fontSize: 14,
        color:"#333333",
        textAlign: "center",
        padding:"5px 2px"
    },
    TextInfoMiniError:{
        backgroundColor:"red",
        fontSize: 14,
        color:"#FFFFFF",
        textAlign: "center",
        padding:"5px 2px"
    },
    titleChildBlock:{
        fontWeight:"bold",
        fontSize:16,
        marginBottom:10
    },
};