import React, { useState,useEffect } from "react";
import axios from "axios";

import {Div, View, Panel, PanelHeader, Group, Search,Cell, CellButton, HeaderButton, platform, IOS, Tooltip, Image, ImageBackground } from '@vkontakte/vkui';
import {ButtonEbee} from "./ButtonEbee";
import {NumberFormatPrice} from "../NumberFormatFN";
require('../../panels/global/index.js');




export const GiftVariantModal = ({sumItogBasket,setActiveGifts,activeGift = false,closeModal}) => {

    const URL_SITE = global.URLCite;

    const [statusLoading,setStatusLoading] = useState(true);


    const [GiftDataServer,setGiftsList] = useState({});


    const getGiftInfo = async ()=>{

        return new Promise((resolve, reject) => {
            axios({
                method:'get',
                url:'https://-./api/getGiftsJson&apiKey='+global.ApiKey+global.CityIdSystem,
                headers: { 'Content-Type': 'application/x-www-form-urlencoded' },
                params:{
                },
                responseType: 'text'
            }).then(({ data }) => {
                console.log("       ",data);
                resolve(data);
            })
                .catch((err) => {
                    //console.log("ERROR: ------  ",err);
                    reject("ERROR: ---  ---  ",err);
                });
        });

    };


    const ActiveIndicateLoading = ()=>{
        return(
            <div style={{justifyContent:"center",alignItems:"center",height:"100%"}}>
                {/*<ActivityIndicator color={"#ff7300"} />*/}
                ...
                <div style={{height:"1200px"}}></div>
            </div>
        )
    }


    const setActiveGift = (dataGift)=>{
        // dispatch(updateActiveGifts(dataGift));
        setActiveGifts(dataGift);
        closeModal(null);
    };

    useEffect(()=>{
        (async ()=>{
            let result = await getGiftInfo();
            setGiftsList(result);
            setStatusLoading(false);
        })()
    },[]);

    const getWidthScale = (sumProduct,dataGift,activeBlock)=>{
        let ReturnValue = false;
        let MaxValue = sumProduct;
        let NowValue = MaxValue-sumItogBasket;

        if(NowValue<=0){
            if(activeBlock === true){
                ReturnValue =
                    <ButtonEbee
                        onPress={()=>{
                            setActiveGift(false);
                        }}
                        size="l"
                        borderRadius={20}
                        shadow={false}
                        style={{paddingVertical:8,paddingHorizontal:30,width:"100%"}}
                    >
                        
                    </ButtonEbee>
                ;
            }else{
                ReturnValue =
                    <ButtonEbee
                        onPress={()=>{
                            setActiveGift(dataGift);
                        }}
                        size="l"
                        borderRadius={20}
                        shadow={false}
                        style={{paddingVertical:8,paddingHorizontal:30,width:"100%"}}
                    >
                        
                    </ButtonEbee>
                ;
            }

        }else{
            if(activeBlock === true){
                setActiveGift(false);
            }
            ReturnValue =
                <ButtonEbee
                    onPress={()=>{

                    }}
                    size="l"
                    borderRadius={20}
                    shadow={false}
                    active={false}
                    style={{paddingVertical:8,marginVertical:0,width:"100%"}}
                >
                     : {NumberFormatPrice(NowValue,0,'',' ')} 
                </ButtonEbee>;
        }




        return ReturnValue;
    };


    const ListGifts = ()=>{

        let AllGiftList = Object.keys(GiftDataServer.OneGift);

        const ReturnListGifts = AllGiftList.map((data,number)=>{

            let AllGiftsStep = Object.keys(GiftDataServer.OneGift[data]);

            let ListGiftsStep = AllGiftsStep.map((val,num)=>{

                let OneGifts = GiftDataServer.OneGift[data][val];

                let activeBlock = false;

                if(activeGift !== false){
                    activeGift.id_tovar == OneGifts.id_tovar & activeGift.type_product == OneGifts.type_product ? activeBlock = true : activeBlock = false;
                }else{
                    activeBlock = false;
                }

                return (
                    <div style={activeBlock === true ? {...styles.BlockListGifts,...styles.BlockListGiftsActive} : styles.BlockListGifts}>
                        <div style={styles.BlockImg}>
                            <img style={styles.image} src={URL_SITE+'/images/tovars/'+OneGifts.img} />
                        </div>
                        <div style={styles.textWrap}>
                            <div style={styles.TextBlockProduct}>
                                <div style={styles.BlockTitle}>
                                    <p style={styles.title}>
                                        {OneGifts.title.trim()}
                                    </p>
                                    <p style={styles.weight}>{OneGifts.type_product_text.trim()}</p>
                                </div>
                                {OneGifts.text !== '' ? <div style={styles.ViewDescription}>
                                    <p style={styles.description}>
                                        {OneGifts.text.trim()}
                                    </p>
                                </div> : <p></p>}
                                <div style={styles.blockButtonSet}>
                                    <p style={{...styles.description,...{fontSize:12,fontFamily:"open-regular"}}}>-: {OneGifts.count_product}</p>
                                    {getWidthScale(OneGifts.min_sum,OneGifts,activeBlock)}
                                </div>
                            </div>
                        </div>
                    </div>
                )
            });

            return ListGiftsStep;

        });

        return ReturnListGifts;
    };

    const FlatListData =
        statusLoading ? <ActiveIndicateLoading /> : <ListGifts />;


    return (
        <div
            scrollEnabled={true}
            style={styles.containerContent}
        >
                        {FlatListData}

        </div>
    );
};

