import {PromokodeClass} from "./PromokodeClass";


let countProductListBasket = 0;

    export const handleSetProductArrayPromokode = (Product, type_price, countProductBasket, Type_Product, idProduct, return_default = true, PromoData) => {
        //     Product, //    (        
        //     SumReturn = 0, //    
        //     type_price, //    ( )
        //     countProductBasket, //-     
        //     Type_Product, //  type_product 0,1,2,3  all
        //     PromoData = {}, //        
        //     countProductListBasket = 0, //-        
        //     idProduct = 0 // id    

        //console.log(Product);

        let ResultCHeckPromokode = PromokodeClass.getPriceFromPromokode(Product, 0, type_price, countProductBasket, Type_Product, PromoData, countProductListBasket, idProduct, return_default);

        console.log('  ', ResultCHeckPromokode);

        return ResultCHeckPromokode;
    };

//   ,     ()  ,      -   .
//          ,        

    export const getTypeProductData = (type_product, ProductDataAll) => {
        let Returns = {};
        ProductDataAll.forEach((type, number) => {
            if (type_product === type.type_product) {
                Returns = type;
            }
        });

        return Returns;
    };

    export const getBasketPromokodeActive = async (def = true, basket = [], allProducts = [], PromoData, handleSetAddDiscountPromokodeData = ()=>{}) => {
        //  console.log('def = ',def, PromokodeErrorInfo.showIconError);
        let ReturnBasketArray = [];
        let AllDiscount = 0;
        let AllPrice = 0;
        await basket.forEach((type, number) => {
            //  console.log(' ',type.id);

                // console.log(allProducts[type.id_Category].ProductList[type.id]);
                let TypeProductData = getTypeProductData(type.type_product, allProducts[type.id_Category].ProductList[type.id_Product]);
                // console.log('      ----======-----======-=-=-=-----====',TypeProductData);
                let ReturnResult = handleSetProductArrayPromokode(TypeProductData, TypeProductData.price, type.count, type.type_product, type.id_Product, def, PromoData);
                // console.log('        : -==--==--==--==--==--==--==--==--==--==--==--==--==--==--==--==--==-',ReturnResult);
                countProductListBasket = ReturnResult.countProductListBasket;


                let PriceDef = TypeProductData.price * type.count;
                AllPrice = AllPrice + PriceDef;
                let PriceDiscount = PriceDef - ReturnResult.SumResult;
                PriceDiscount < 0 ? PriceDiscount = 0 : PriceDiscount = PriceDiscount;
                AllDiscount = AllDiscount + PriceDiscount;

                let ArrayPush = {
                    id_product: type.id_Product,
                    type_product: type.type_product,
                    sumDiscount: PriceDiscount, //   
                    sumPriceDef: PriceDef, //   
                    sumPriceSale: ReturnResult.SumResult, //    
                    count: type.count
                };

                ReturnBasketArray.push(ArrayPush);

        });

        if (PromoData !== null) {
            if (PromoData.dop_settings.type_summa_check == '0') {

                let Check1 = (AllPrice - AllDiscount)*1;
                let Check2 = PromoData.dop_settings.summa_min_active*1;

                if (Check1 < (Check2)) {
                    AllDiscount = 0;
                    ReturnBasketArray = [];
                    // console.log(' ,  ', PromoData.dop_settings.type_summa_check);
                    // console.log('Check1 < Check2', Check1,  Check2);
                }
            }
        }

        let DispatchData = {
            allSumDiscount: AllDiscount,
            arrayProductDiscount: ReturnBasketArray,
            // sumPreorderToppting: SumReturnsTopping
            sumPreorderToppting: 0
        };

        // console.log('   : ',ReturnBasketArray, '  : ',AllDiscount);
        // dispatch(setArrayPricePromokodeProduct(DispatchData));
        console.log('      ',def,DispatchData);
        handleSetAddDiscountPromokodeData(DispatchData);
    };

    export const getSumItogButton = (PromokodeErrorInfo,PromokodeActiveStatus,sumItogBasket,DataPromokodeDiscountProduct, handleReturnSumBasket = ()=>{}) => {
        // let sumItog = sumItogBasket-DataPromokodeDiscountProduct.allSumDiscount;
        let sumItog = 0;
        if (PromokodeErrorInfo.showIconError === true) {
            sumItog = sumItogBasket;
        } else {
            if (PromokodeActiveStatus) {
                sumItog = sumItogBasket - DataPromokodeDiscountProduct.allSumDiscount;
                console.log('      ',sumItogBasket,DataPromokodeDiscountProduct,DataPromokodeDiscountProduct.allSumDiscount,sumItog);
            } else {
                sumItog = sumItogBasket;
            }
        }
        handleReturnSumBasket(sumItog);
        return true;
    };
