import React from 'react';
import {Alert, Div,Input,FormLayout, Button, Select, Search, Footer, Cell } from '@vkontakte/vkui';


export const PromokodeInput = ({value = '',setValue})=>{


    return (
        <Input
            style={styles.promokodeInput}
            placeholder={"0000"}
            type="text"
            name="street"
            key={"input_street"}
            value={value}
            status='valid'
            onChange={(text) => {
                console.log(text.target.value);
                setValue(text.target.value.replace(/[--\s+]/g, ''))
            }}
            // onClick={()=>{this.setState({search:this.state.streetSearch,street:'',streetSearch:''})}}
        />
    )

    // return (
    //     <TextInput
    //         style={styles.promokodeInput}
    //         placeholder={"0000"}
    //         onChangeText={(text) => setValue(text.replace(/[--\s+]/g, ''))}
    //         maxLength={7}
    //         value={value}
    //         multiline={false}
    //         textAlign={'center'}
    //         keyboardType={Platform.OS === 'android' ? "default" : "default"}
    //
    //     />
    //
    // )
};


