import React, {useEffect,useState} from "react";
import axios from "axios";
import {ButtonEbee} from "./ButtonEbee";
require('../../panels/global/index.js');


export const ScaleGift = (props)=> {
    let Options = {
        ...{
            title: false,
            bottomLabel: false,
            giftsList:{},
        },
        ...props
    };


    const [GiftDataServer,setGiftsList] = useState({});


    const getGiftInfo = ()=>{
        axios({
            method:'get',
            url:'https://-./api/getGiftsJson&apiKey='+global.ApiKey+global.CityIdSystem,
            responseType:'text'
        }).then(({ data }) => {
            console.log("      ",data);
            setGiftsList(data);
        })
            .catch((err) => {
                console.log("ERROR: --   ----  ",err);
            });
        //setUserSystem(e);
    };


    useEffect(()=>{
        getGiftInfo();
    },[])


    const sumItogBasket = Options.sumBasket;
    const activeGift = Options.activeGift;
    ////
    //

    const getSumActiveGift = (sumProduct)=>{
        if(activeGift !== false){
            let MaxValue = activeGift.min_sum;
            let NowValue = MaxValue-sumItogBasket;

            if(NowValue<=0) {
                return true;
            }else{
                setActiveGift(false);
                return false;
            }
        }else{

        }

    };

    const getWidthScale = ()=>{
        let MinValue = 0;
        let MaxValue = GiftDataServer.settingsScale.max_summa;
        let NowValue = (sumItogBasket*100)/MaxValue;
        if(NowValue>100){NowValue = 100;}

        // console.log(' '+sumItogBasket+'   '+MaxValue+'  '+NowValue);
        return NowValue+"%";
    };


    const getCountNoSum = ()=>{
        let NoSum = 0;

        let AllGiftStep = GiftDataServer.ListGift;

        let NumbersGiftStep = Object.keys(AllGiftStep);

        NoSum = AllGiftStep[NumbersGiftStep[0]].min_price - sumItogBasket;

        if(NoSum<0){NoSum = 0;}

        return NoSum;
    };

    const setActiveGift = (dataGift)=>{
        if(activeGift !== false) {
            // dispatch(updateActiveGifts(dataGift));
            console.log('  ');
            Options.setActiveGifts(dataGift);
        }
    };

    const getActiveGiftBlock = ()=>{
        return(
            <div style={{width:"100%",        display:"flex",
                flexDirection:"row",justifyContent:"center",alignItems:"center",paddingVertical:5,flexWrap:"wrap",borderBottomColor:"#fafafa",borderBottomWidth:3,borderTop:"8px solid #eeeeee"}}>
                <div style={{width:"95%",        display:"flex",
                    flexDirection:"row",justifyContent:"space-between",alignItems:"center",paddingVertical:5}}>
                    <div style={{}}>
                        <img style={{width:110,height:110}} src={global.URLCite+'/images/tovars/'+activeGift.img} />
                    </div>
                    <div style={{width:"100%"}}>
                        <div style={{width:"100%",padding:"10px 0"}}>
                            <div style={{width:"100%",        display:"flex",
                                flexDirection:"row",justifyContent:"space-between",alignItems:"center"}}>
                                <div style={{width:"70%",textAlign:"left",paddingLeft:5}}>
                                    <span style={{fontWeight:"bold",fontSize:14}}>{activeGift.title.trim()}</span>
                                </div>
                                <div style={{width:"25%",textAlign:"right"}}>
                                    <span style={{fontWeight:"bold",fontSize:12}}>{activeGift.type_product_text}</span>
                                </div>
                            </div>
                            <div>
                                <div style={{fontWeight:300,fontSize:12,paddingRight:0,flexWrap:"wrap",textAlign:"left",paddingTop:15,paddingLeft:5}}>{activeGift.text.trim()}</div>
                            </div>
                        </div>
                        <div style={{width:"100%",padding:"0px 0"}}>
                            <div style={{fontWeight:300,fontSize:12,paddingRight:0,flexWrap:"wrap",padding:"0 2px",textAlign:"left"}}>-: {activeGift.count_product}</div>
                        </div>
                    </div>
                </div>
                <div style={{flexDirection:"row",        display:"flex",
                    justifyContent:"space-between",alignItems:"center",width:"90%",padding:"0 7px",borderTopColor:"#eeeeee",borderTopWidth:1}}>
                    <div style={{}}>
                        <span>!</span>
                    </div>
                    <div style={{width:120}}>
                        <ButtonEbee
                            onPress={()=>{
                                setActiveGift(false);
                            }}
                            size="l"
                            borderRadius={20}
                            shadow={false}
                            active={true}
                            style={{paddingVertical:8,marginVertical:0}}
                        >
                            
                        </ButtonEbee>
                    </div>
                </div>
            </div>
        )
    };


    const getGiftNoSum = ()=>{

        let NoSum = getCountNoSum();

        if(NoSum == 0){
            return (
                <div style={styles.textGiftNoSum}>
                    {activeGift !== false ?
                        getActiveGiftBlock()
                        :
                        Options.PromokodeActiveStatus ? <p style={styles.textGiftNoSumText}>    ! <br />     ,  !</p> : <p style={styles.textGiftNoSumText}>,    !</p>}
                </div>
            );
        }else{
            setActiveGift(false);
            return (
                <div style={styles.textGiftNoSum}>
                    {Options.PromokodeActiveStatus ? <p style={styles.textGiftNoSumText}>    ! <br />     ,  !</p> :  <p style={styles.textGiftNoSumText}>      : {NoSum}</p>}
                </div>
            );
        }

    };

    const checkActivePromokode =()=>{
        if(Options.PromokodeActiveStatus === true){
            if(activeGift !== false){
                setActiveGift(false);
            }
        }else{

        }
    };

    useEffect(()=>{
        getSumActiveGift();
        checkActivePromokode();
    },[sumItogBasket,Options.PromokodeActiveStatus]);

    const getButtonGift = ()=>{
        let NoSum = getCountNoSum();
        if(NoSum == 0){
            return (
                <div style={styles.ButtonBlock}>
                    {Options.PromokodeActiveStatus ?
                        <ButtonEbee
                            onPress={() => {
                                console.log('      ');
                            }}
                            size="l"
                            borderRadius={20}
                            shadow={false}
                            active={false}
                            style={{color: "#252722"}}

                        >
                            
                        </ButtonEbee>
                        :
                        <ButtonEbee
                            onPress={() => {
                                console.log('    ');
                                Options.showModal('MODAL_GIFTS');
                            }}
                            size="l"
                            borderRadius={20}
                            shadow={false}
                            style={{color: "#252527", backgroundColor: "#ffffff"}}
                        >
                            {activeGift !== false ? "" : ""}
                        </ButtonEbee>
                    }
                </div>
            )
        }else{
            return (
                <div style={styles.ButtonBlock}>
                    <ButtonEbee
                        onPress={()=>{
                            console.log('      ');
                        }}
                        size="l"
                        borderRadius={20}
                        shadow={false}
                        active={false}
                        style={{color:"#252722"}}

                    >
                        
                    </ButtonEbee>
                </div>
            )
        }

    };


    if("ready" in GiftDataServer){
        if(GiftDataServer.ready){
            getWidthScale();
            // getGiftNoSum();
            return(
                <div>
                    <div style={{...styles.View,...props.styles}}>
                        <div style={styles.imagePassive}>
                            <div style={{position:"absolute",backgroundColor:"#fafafa",overflow:"hidden",width:getWidthScale()}}>
                                <div  style={styles.image}>
                                </div>
                            </div>
                            <div style={{width:"100%",display:"flex",flexDirection:"row",justifyContent:"center",alignItems:"center",position:"absolute",height:"110px"}}>
                                {getButtonGift()}
                            </div>
                        </div>
                    </div>
                    {getGiftNoSum()}
                </div>
            );
        }else{
            return false;
        }
    }else{
        return false;
    }

};

const styles = {
    View:{
        backgroundColor:"#fafafa",
        width:"100%",
        height:120,        display:"flex",

        justifyContent:"space-between",
        alignItems:"center",
        flexDirection:"row",
        zIndex:99,
        backgroundImage: 'url("https://-./images/system_icons/api_gift/pattern_gift_mobile_passive1.jpg")',
        backgroundSize:"80%"
    },
    h2:{
        fontSize:18,
        textTransform:"uppercase"
    },
    shadow:{
        borderWidth:0,
        shadowColor: "#000",
        shadowOffset: {
            width: 0,
            height: 2,
        },
        shadowOpacity: 0.22,
        shadowRadius: 0,
        elevation: 3,
        marginBottom:10
    },
    shadowOff:{
        borderWidth:0,
        shadowColor: "#000",
        shadowOffset: {
            width: 0,
            height: 0,
        },
        shadowOpacity: 0.22,
        shadowRadius: 0,
        elevation: 0,
        marginBottom:10
    },
    image:{
        flex: 1,
        flexDirection:"row",
        display:"flex",

        justifyContent: "flex-start",
        alignItems:"center",
        width:400,
        height:120,
        backgroundImage:'url("https://-./images/system_icons/api_gift/pattern_gift_mobile_active1.jpg")',
        backgroundSize:"79%"

    },
    imagePassive:{
        // flex: 1,
        // flexDirection:"row",
        display:"flex",
        justifyContent: "flex-start",
        alignItems:"center",
        width:400,
        height:120,
        alignSelf: "flex-start",
    },
    textGiftNoSum:{
        display:"flex",
        flexDirection:"row",
        textAlign: "center",
        justifyContent:"center",
        alignItems:"center"
    },
    textGiftNoSumText:{
        width:"92%",
        textAlign:"center",
        fontSize: 13,
        paddingBottom:"10px",
        fontWeight:600,
        paddingTop:10
        // borderBottom:"1px solid #eeeeee",
    },
    ButtonBlock:{
        width:140,
        margin:"auto",
        zIndex:2
    }
};