import React, { useState } from "react";

import {PromokodeInput} from "./PromokodeInput";
import {Div, View, Panel, PanelHeader, Group, Search,Cell, CellButton, HeaderButton, platform, IOS, Tooltip, Image, ImageBackground } from '@vkontakte/vkui';
import {ButtonEbee} from "../Cart/ButtonEbee";
import {ConditionsPromokod} from "./ConditionsPromokode";
import axios from "axios";


require('../../panels/global/index.js');



export const PromokodeModal = ({
                                   ErrorPromokode,
                                   handleSetPromokodeError,
                                   PromokodeActiveStatus,
                                   handleSetActiveStatusPromokode,
                                   PromokodeDate,
                                   handleSetDataPromokode,
                                   promokodeErrorInfo,
                                   handleSetPromokodeErrorInfo,
                                   handleDeactivatePromokodes
}) => {

    // const ErrorPromokode = useSelector(state=>state.post.promokodeError);
    // const PromokodeActiveStatus = useSelector(state=>state.post.promokodeActive);
    // const PromokodeDate = useSelector(state=>state.post.promokodeData);

    // const CityIds = useSelector(state=>state.post.cityData.cityId);
    // const idUserSystem = useSelector(state=>state.post.AuthUserData.id);
    const CityIds = 1;
    const idUserSystem = 3;

    //const sumItogBasket = useSelector(state => state.post.basketSum);


    let [statusLoadingPromokode, setstatusLoadingPromokode] = useState(false);
    let [kode, setKode] = useState('');
    let [errorInformationMessage, setErrorMessages] = useState(null);

    //console.log(StatusAllModal);

    const ActiveIndicateLoading = ()=>{
        return(
            <div style={{justifyContent:"center",alignItems:"center",height:"50%"}}>
                {/*<ActivityIndicator color={THEME.MAIN_COLOR} />*/}
                <p>...</p>
                <div style={{height:"600px"}}></div>
            </div>
        )
    }

    const handleSetStatusModal = (nameModal,status)=>{
        // dispatch(setModalStatus(nameModal,status));
    };

    const ErrorPromokodeInfo = ()=>{
        if(ErrorPromokode.status === false){
            return false;
        }else{
            return (
                <div style={styles.ChildBlock}>
                    <div style={{...styles.TextInfoMiniError,...{textAlign: "center"}}}>: {ErrorPromokode.message}</div>
                </div>
            )
        }
    };

    const handleDeactivatePromokode = ()=>{
        setKode(null);
        // dispatch(setPromokodeUpdate(null,false));
        handleDeactivatePromokodes()
    }


    const PromokodeInfoServer = ()=>{
        // console.log(PromokodeDate);

        if(PromokodeActiveStatus === false){
            return false;
        }else{
            return (
                <>
                    {FlatListDataActivePromo}

                    <Div
                        styles={{
                            position:"absolute",
                            // top:Dimensions.get('window').height*0.6,
                            bottom:"2%",
                            backgroundColor:"#ffffff",
                            paddingBottom: "10%",
                            width:"100%"
                        }} shadow={false}
                    >
                        <ButtonEbee
                            onPress={()=>{
                                handleDeactivatePromokode()
                            }}
                            size="xl"
                            borderRadius={20}
                            shadow={true}
                        >
                            
                        </ButtonEbee>
                    </Div>
                </>
            )
        }
    };

    const FlatListData =
            <div style={styles.modalContent}>
                <div style={styles.TitleModal}>
                    <div style={styles.modalTextTitle}> </div>
                </div>
                <div style={styles.ChildModal}>
                    <div style={{...styles.ChildBlock,...{margin:0}}}>
                        <PromokodeInput value={kode} setValue={setKode}/>
                    </div>
                    <div style={styles.ChildBlock}>
                        <div style={{...styles.TextInfoMini,...{textAlign: "center",backgroundColor:"#ff7300"}}}>     *</div>
                    </div>
                    {statusLoadingPromokode ? ActiveIndicateLoading() : ErrorPromokodeInfo()}
                </div>
            </div>
        ;


    const DataPromokodeInfo = ()=>{
        return (
            PromokodeDate !== null && PromokodeDate !== undefined && PromokodeDate != 'undefined' ?
                <div style={styles.BorderRadiusBox}>
                    <div style={{...styles.TextInfoMiniOk,...{textAlign: "center"}}}>
                         : {PromokodeDate.promokodeInfo.kode}
                    </div>
                    <div style={{...styles.TextInfoMiniOk,...{textAlign: "center"}}}>
                        {PromokodeDate.promokodeInfo.text}
                    </div>
                </div> :
                <div style={{...styles.TextInfoMiniError,...{textAlign: "center"}}}>
                    :     . ,  ,    !
                </div>
        )
    }

    const FlatListDataActivePromo =
            <div style={styles.modalContent}>
                <div style={styles.TitleModal}>
                    <div style={styles.modalTextTitle}> </div>
                </div>
                <div style={styles.ChildModal}>
                    <div style={styles.ChildBlock}>
                        {DataPromokodeInfo()}
                    </div>
                    <div style={styles.ChildBlock}>
                        <ConditionsPromokod typeReturn={1} promokodeErrorInfo={promokodeErrorInfo}/>
                        <ConditionsPromokod typeReturn={2} promokodeErrorInfo={promokodeErrorInfo}/>
                    </div>
                </div>
            </div>
    ;

    const getPromokodeActive = (CityData,idUser,kode)=>{
        // console.log('  ');
        return new Promise((resolve, reject) => {
            axios({
                method:'get',
                url:'https://-./api/getPromokodeActive&apiKey='+global.ApiKey+'&CityIdSystem='+CityData,
                headers: { 'Content-Type': 'application/x-www-form-urlencoded' },
                params:{
                    idUser:idUser,
                    kode:kode
                },
            }).then(({ data }) => {
                if('error' in data){
                    console.log(" ",data);
                    resolve({error:true,errorInfo:data.errorInfo});
                }else{
                    //console.log("  ",data.listProduct);
                    resolve({error:null,errorInfo:null,result:data.result});
                }
                //console.log(this.state.Metrika);
                //console.log(data.list);
            })
                .catch((err) => {
                    console.log("ERROR: ------  ",err);
                    reject({error:true,errorInfo:"  "});
                });
        });
    }

    const handleActivePromokode = async (idCity,idUser)=>{
        setstatusLoadingPromokode(true);
        //  
        const ReultServer = await getPromokodeActive(idCity,idUser,kode);

        setTimeout(()=>{
            setstatusLoadingPromokode(false);
        },1000);


        if(ReultServer.error === null){
            console.log('    '+kode, ReultServer);
            handleSetPromokodeError({error:false,message:null});
            handleSetActiveStatusPromokode(true);
            handleSetDataPromokode(ReultServer.result);
            // handleSetPromokodeErrorInfo(null)
            //     // dispatch(setPromokodeErrorUpdate(false,null));
        //     // dispatch(setPromokodeUpdate(ReultServer.result,true));
        //
        }else{
            console.log('   '+kode, ReultServer);
            handleSetPromokodeError({error:ReultServer.error,message:ReultServer.errorInfo})
            handleSetActiveStatusPromokode(false)
            handleSetDataPromokode(null)
            handleSetPromokodeErrorInfo(null)

            //     // dispatch(setPromokodeErrorUpdate(true,ReultServer.errorInfo));
        //     // dispatch(setPromokodeUpdate(null,false));
        }

        //   console.log('  -   - ==========>>>>>>>>',ReultServer);
    };

    return (

                <div
                    style={styles.containerContent}
                >
                    {PromokodeActiveStatus ? PromokodeInfoServer() :
                        <>
                            {FlatListData}
                            <Div
                                styles={{
                                    position:"absolute",
                                    // top:Dimensions.get('window').height*0.6,
                                    bottom:"18%",
                                    backgroundColor:"#ffffff",
                                    paddingBottom: "10%",
                                    width:"100%"
                                }}
                            >
                                <ButtonEbee
                                    onPress={()=>{
                                        handleActivePromokode(CityIds,idUserSystem);
                                    }}
                                    size="xl"
                                    borderRadius={20}
                                    shadow={true}
                                >
                                    
                                </ButtonEbee>
                            </Div>
                        </>
                    }
                </div>
    );
};

const styles = {
    centeredViewActive: {
        flex: 1,
        display:"flex",
        justifyContent: "center",
        alignItems: "center",
        marginTop: 0,
        // backgroundColor:"rgba(0,0,0,0.40)"
    },
    centeredView: {
        flex: 1,
        display:"flex",
        justifyContent: "center",
        alignItems: "center",
        marginTop: 10,
        //backgroundColor:"rgba(0,0,0,0.40)"
    },
    PressableClose:{
        height:"100%",
        width:"100%"
    },

    ChildBlock:{
        width:"100%",
        boxSizing:"border-box",
        margin:"10px 0px"
    },
    TextInfoMini:{
        backgroundColor:"#ff7300",
        fontSize: 14,
        color:"#FFFFFF",
        textAlign: "center",
        padding:"5px 2px",
        margin:"10px 0"
    },
    BorderRadiusBox:{
        backgroundColor:"#FFFFFF",
        borderRadius:10,
        overflow:"hidden",
        padding:"2px 10px"
    },
    TextInfoMiniOk:{
        backgroundColor:"#FFFFFF",
        fontSize: 14,
        color:"#333333",
        textAlign: "center",
        padding:"5px 2px",
    },
    TextInfoMiniError:{
        backgroundColor:"red",
        fontSize: 14,
        color:"#FFFFFF",
        textAlign: "center",
        padding:"5px 2px",
    },
    titleChildBlock:{
        fontWeight:"bold",
        fontSize:16,
        marginBottom:10
    },

    centeredViewWindow: {
        flex: 1,
        display:"flex",
        justifyContent: "center",
        alignItems: "center",
        marginTop: 0,
        backgroundColor:"rgba(0,0,0,0.40)"
    },
    modalView: {
        width:"100%",
        height:"80%",
        margin: 0,
        backgroundColor: "white",
        borderRadius: 20,
        padding: 0,
        position:"absolute",
        alignItems: "center",
        bottom:0,
        shadowColor: "#000",
        shadowOffset: {
            width: 0,
            height: 2
        },
        shadowOpacity: 0.25,
        shadowRadius: 4,
        elevation: 5
    },

    topIndicator:{
        width:40,
        height:3,
        borderRadius:10,
        marginTop:4,
        marginBottom:4,
        backgroundColor:"#dddddd"
    },
    modalContent:{
        backgroundColor:"#ffffff",
        width:"100%",
        padding:"3px 3%",
        paddingBottom:"20%",
        boxSizing: "border-box"
    },

    TitleModal:{
        width:"100%",
        display:"flex",
        flexDirection:"row",
        justifyContent:"center",
        alignItems:"center",
        fontSize:18,
        //backgroundColor:"#ff7300"
    },
    modalTextTitle:{
        // backgroundColor:"#ff7300",
        textAlign:"center",
        fontWeight:"bold",
        fontSize:18,
        marginBottom:10
    },

    ChildModal:{
        marginVertical:0
    },






    containerContent: {
        flex: 1,
        marginTop: 0,
        padding:0,
        paddingBottom:"40%",
        backgroundColor:"#fafafa",
    },
    containerHeader: {
        flex: 1,
        alignContent: 'center',
        alignItems: 'center',
        justifyContent: 'center',
        height: 30,
        backgroundColor: "#fafafa",
        borderTopLeftRadius:10,
        borderTopRightRadius:10
    },
    headerContent:{
        marginTop: 0,
    },
    Modal: {
        backgroundColor: '#005252',
        marginTop: 0,
    }
};