import {GetPriceProductBasketNumber} from "./getPriceProductBasket";


export const GetAllPriceBasketDontTopping = (basket = [],allProducts =[], PromoData = [], returnBolean = false)=>{

    if(allProducts === null || PromoData === [] || PromoData === null || PromoData === undefined || PromoData == 'undefined'){
        return false;
    }

    let AllSum = 0;
    let Bolean = false;

    console.log(PromoData);


    basket.forEach((typeProducts,num)=>{
        // console.log('  ',typeProducts,allProducts,basket);


            let product = [];

            if(typeProducts.id_Category in allProducts){
                //  
                if(typeProducts.id_Product in allProducts[typeProducts.id_Category]['ProductList']){
                    //  
                    if(allProducts[typeProducts.id_Category]['ProductList'][typeProducts.id_Product].lenght<=0){
                        //return false;
                    }else{
                        let statusTypeProduct = false;
                        let numberArr = 0;
                        allProducts[typeProducts.id_Category]['ProductList'][typeProducts.id_Product].forEach((dataProduct, numb) => {
                            if(typeProducts.type_product == dataProduct['type_product']){
                                //    
                                //     ------>>>
                                statusTypeProduct = true;
                                numberArr = numb;
                            }
                        });

                        if(statusTypeProduct === false){
                            // console.log(" ",typeProducts.type_product);
                            //return AllSum;
                        }else{
                            product = {...allProducts[typeProducts.id_Category]['ProductList'][typeProducts.id_Product][numberArr],id:typeProducts.id_Product};

                            //----->>>>

                            let SumProduct = GetPriceProductBasketNumber(product,typeProducts.count);
                            let BoleanDiscount = GetPriceProductBasketNumber(product,typeProducts.count,true);

                            if(BoleanDiscount === true && returnBolean === true){
                                Bolean = true;
                            }

                            //console.log(SumProduct);
                            AllSum += SumProduct*1;
                        }
                    }

                }
            }
    });

    if(returnBolean === true) {
        return Bolean;
    }

    return AllSum;
};
