import React from "react";

export const ButtonEbee = ({children,
                               onPress = console.log(' '),
                               size = 'xl', //l - , xl -  , xxl - ,
                               active = true, //true - , false -  
                               borderRadius = 0,
                               marginBottom = 0,
                               shadow = false,
                               style = {}
                           })=>{

    const getSizeButton = (size)=>{
        switch (size) {
            case 'l' : return styles.lButton;
            case 'xl' : return styles.xlButton;
            case 'xxl' : return styles.xxlButton;
            default: return styles.xlButton;
        }
    };

    //console.log(active);

    const getTypeButton = (active)=>{
        switch (active) {
            case true : return styles.allButtonStyle;
            case false : return styles.noActiveButton;
            default: return styles.allButtonStyle;
        }
    };

    let Shadows = styles.shadow;

    if(shadow === false){
        Shadows = {...styles.shadowOff,...{marginBottom:marginBottom}};
    }else{
        Shadows = {...styles.shadow,...{marginBottom:marginBottom}};
    }

    return(
        <div
            onClick={onPress}
            style={{...styles.overflowHidden}}
        >
            <div style={{...Shadows,...styles.allButtonStyle,...{borderRadius:borderRadius},...getSizeButton(size),...getTypeButton(active),...style}}>
                {children}
            </div>
        </div>
    )
};

const styles = {
    allButtonStyle:{
        backgroundColor:"#FF7300",
        color:"#FFFFFF",
        textAlign:"center",
        margin: 0,
        overflow: "hidden",
        width:"99%",
        display:"inline-block",
        boxSizing:"border-box"
    },
    noActiveButton:{
        backgroundColor:"#eeeeee",
        color:"#333333",
        textAlign:"center",
        margin: 0,
        overflow: "hidden",
        width:"99%"
    },
    overflowHidden:{
        overflow:"hidden",
        padding:"5px 0",
        justifyContent:"center",
        alignItems:"center"
    },
    lButton:{
        fontSize:12,
        padding:"12px 20px",
    },
    xlButton:{
        fontSize:16,
        padding:"12px 20px",
    },
    xxlButton:{
        fontSize:20,
        padding:"12px 20px",
    },

    shadow:{
        borderWidth:0,
        shadowColor: "#000",
        shadowOffset: {
            width: 0,
            height: 3,
        },
        shadowOpacity: 0.20,
        shadowRadius: 0,
        elevation: 3,
        marginBottom:"10px"
    },
    shadowOff:{
        borderWidth:0,
        shadowColor: "#000",
        shadowOffset: {
            width: 0,
            height: 0,
        },
        shadowOpacity: 0.22,
        shadowRadius: 0,
        elevation: 0,
        marginBottom:"10px"
    }
};