import React from "react";
import {GetPriceProductBasketNumber, GetTypePriceProductBasket} from "./getPriceProductBasket";

export class PromokodeClass extends React.Component {

    constructor(props){
        super(props);
        //console.log(' ');
    }


    // static init() {
    //     return (()=>{
    //         console.log('   ');
    //         //this.getSlideHeader();
    //     });
    // }


    static getSalePromoOK(Product) {
        let go_sale_ok = true;
        // if (typeof PromoData == 'undefined') PromoData = {};

        //    .      .
        GetTypePriceProductBasket(Product) ? go_sale_ok = false : go_sale_ok = true;

        return go_sale_ok;
    }


    //   
    static getPriceFromPromokode(
        Product, //    (        
        SumReturn = 0, //    
        type_price, //    ( )
        countProductBasket = 0, //-     
        Type_Product, //  type_product 0,1,2,3  all
        PromoData = {}, //        
        countProductListBasket = 0, //-        
        idProduct = 0,
        return_default = false// id    
    ) {

        // console.log('     ',return_default);

        let countReturnNext = 1;

        type_price = GetPriceProductBasketNumber(Product,countReturnNext,false);

        if(return_default || PromoData === null){
            SumReturn = SumReturn + countProductBasket * type_price;
            // console.log('  ');
            return {"SumResult":parseFloat(SumReturn.toFixed(0)),"countProductListBasket":countProductListBasket};
        }

        let sales = 0;
        let proc_Sum = 0;

        let go_sale_ok = true;

        switch (PromoData.promokodeInfo.sale_type) {
            case "0":
                //  %.
                sales = PromoData.promokodeInfo.sale / 100;
                proc_Sum = type_price * sales;
                break;
            case "1":
                //  .
                sales = PromoData.promokodeInfo.sale;
                proc_Sum = sales;
                if (proc_Sum < 0) {
                    proc_Sum = 0;
                }
                break;
        }

        !return_default ?
            go_sale_ok = this.getSalePromoOK(Product)
            :
            go_sale_ok = false;


        //console.log(' type_price ',Product,countReturnNext,type_price);

//            console.log(type_price);

        //  
        if (PromoData.promokodeInfo.type === "sale_category") {
            // console.log('  ,  ');
            if (go_sale_ok === true) {
                //      (     )
                if (Product.categoryId === PromoData.promokodeInfo.id_category) {
                    //       ,  
                    if (countProductListBasket >= PromoData.productInfo.count_product && PromoData.productInfo.count_product !== 0) {
                        //-          -    (       ),  . 
                        SumReturn = SumReturn + countProductBasket * type_price;
                    } else {
                        //-        ,    ()
                        if (countProductBasket <= PromoData.productInfo.count_product) {
                            //-   ,    -    
                            if (Type_Product === PromoData.productInfo.type_product || PromoData.productInfo.type_product === "all") {
                                //       (       ),  
                                SumReturn = SumReturn + countProductBasket * (type_price - proc_Sum);
                            } else {
                                //        ,  . 
                                SumReturn = SumReturn + countProductBasket * type_price;
                            }
                        } else {
                            //-   ,  -    
                            if (Type_Product === PromoData.productInfo.type_product || PromoData.productInfo.type_product === "all") {
                                //       (       ),  
                                let difference = 0; //    - () 
                                difference = countProductBasket - PromoData.productInfo.count_product;
                                //  -    
                                SumReturn = SumReturn + PromoData.productInfo.count_product * (type_price - proc_Sum);
                                SumReturn = SumReturn + difference * type_price;
                                //     .
                            } else {
                                //        ,  . 
                                SumReturn = SumReturn + countProductBasket * type_price;
                            }
                        }
                        // -   ,   ,             
                        countProductListBasket++;
                    }
                } else {
                    //id      id    ,   
                    SumReturn = SumReturn + countProductBasket * type_price;
                }
            } else {
                //   ,    ,          +
                SumReturn = SumReturn + countProductBasket * type_price;
            }
        }

        //  
        if (PromoData.promokodeInfo.type === "sale_product") {
            // console.log('    ');
            if (go_sale_ok === true) {
                //      (     )
                if (idProduct === PromoData.productInfo.id_product) {
                    //id    id    
                    if (countProductBasket <= PromoData.productInfo.count_product || PromoData.productInfo.count_product === 0) {
                        //-   ,    -    
                        if (Type_Product === PromoData.productInfo.type_product || PromoData.productInfo.type_product === "all") {
                            //       (       ),  
                            SumReturn = SumReturn + countProductBasket * (type_price - proc_Sum);
                        } else {
                            //        ,  . 
                            SumReturn = SumReturn + countProductBasket * type_price;
                        }
                    } else {
                        //-   ,   -    
                        if (Type_Product === PromoData.productInfo.type_product || PromoData.productInfo.type_product === "all") {
                            //       (       ),  
                            let difference = 0; //    - () 
                            difference = countProductBasket - PromoData.productInfo.count_product;
                            //  -    
                            SumReturn = SumReturn + PromoData.productInfo.count_product * (type_price - proc_Sum);
                            SumReturn = SumReturn + difference * type_price;
                            //     .
                        } else {
                            //        ,  . 
                            SumReturn = SumReturn + countProductBasket * type_price;
                        }
                    }
                } else {
                    // console.log('   , ',SumReturn, countProductBasket, type_price);
                    //id     id    
                    SumReturn = SumReturn + countProductBasket * type_price;
                }
            } else {
                //   ,    ,          +
                SumReturn = SumReturn + countProductBasket * type_price;
            }
        }
        //  
        if (PromoData.promokodeInfo.type === "sale_all") {
            //         
            console.log('      ');
            if (go_sale_ok === true) {
                console.log("   ,  ");
                //      (     )
                if (countProductListBasket >= PromoData.productInfo.count_product && PromoData.productInfo.count_product !== 0) {
                    //-          -    (       ),  . 
                    SumReturn = SumReturn + countProductBasket * type_price;
                } else {
                    //-        ,    ()
                    if (countProductBasket <= PromoData.productInfo.count_product || PromoData.productInfo.count_product === 0) {
                        //-   ,    -    
                        if (Type_Product === PromoData.productInfo.type_product || PromoData.productInfo.type_product === "all") {
                            //       (       ),  
                            SumReturn = SumReturn + countProductBasket * (type_price - proc_Sum);
                        } else {
                            //        ,  . 
                            SumReturn = SumReturn + countProductBasket * type_price;
                        }
                    } else {
                        //-   ,   -    
                        if (Type_Product === PromoData.productInfo.type_product || PromoData.productInfo.type_product === "all") {
                            //       (       ),  
                            let difference = 0; //    - () 
                            difference = countProductBasket - PromoData.productInfo.count_product;
                            //  -    
                            SumReturn = SumReturn + PromoData.productInfo.count_product * (type_price - proc_Sum);
                            SumReturn = SumReturn + difference * type_price;
                            //     .
                        } else {
                            //        ,  . 
                            SumReturn = SumReturn + countProductBasket * type_price;
                        }
                    }
                }
                // -   ,   ,             
                countProductListBasket++;
            } else {
                //   ,    ,          +
                console.log('    ,   ');
                SumReturn = SumReturn + countProductBasket * type_price;
            }
        }


        return {"SumResult":parseFloat(SumReturn.toFixed(0)),"countProductListBasket":countProductListBasket};
    }
}